.floating-Div {
    justify-content: space-around;
    background: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    display: flex;
    align-items: center;
    padding: 0px 28px 0px 0px;
    height: 4.5rem;
  }
  .floating-Div > img {
    transform: scale(0.4);
  }
  .floating-Div > span {
    font-family: sans-serif;
    font-size: 16px;
  }
  
 
  